.upper-menu {
  list-style-type: none;
  margin-right: 150px;
  display: flex;
  padding-left: 0; }
  @media screen and (max-width: 512px) {
    .upper-menu {
      display: none; } }
  @media (min-width: 512px) and (max-width: 1120px) {
    .upper-menu {
      order: 999;
      margin-left: auto; } }
  .upper-menu__item {
    display: inline;
    margin-right: 2vw;
    display: inline-block;
    background: #FF3E79;
    padding: 10px 9px 10px 11px;
    text-decoration: none;
    border-radius: 100%;
    vertical-align: top;
    width: 43px;
    height: 43px; }
    .upper-menu__item:hover {
      opacity: 0.5; }
  .upper-menu__img--chat {
    margin-top: 1px; }
  .upper-menu__img--call {
    margin-top: 1px; }

.upper-menu {
  order: 999;
  margin-left: auto; }
