.technique {
  box-shadow: inset 0 2px 4px #D9FFF5;
  background-color: #F8F8F8; }
  @media screen and (max-width: 768px) {
    .technique::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      box-shadow: inset -40px 0 40px -4px white;
      z-index: 2;
      pointer-events: none; } }
  .technique__list {
    list-style-type: none;
    padding-left: 24px;
    padding-right: 24px; }
    @media screen and (max-width: 767px) {
      .technique__list {
        padding-left: 0; } }
    @media screen and (min-width: 767px) {
      .technique__list {
        display: flex;
        flex-wrap: wrap;
        gap: 2vw;
        flex: auto auto;
        height: 170px;
        overflow: hidden;
        margin-bottom: 60px; } }
  .technique__item {
    background-color: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    display: flex;
    justify-content: space-between; }
    @media screen and (min-width: 768px) {
      .technique__item {
        flex: 0 1 31.9%; } }
    @media screen and (min-width: 950px) {
      .technique__item {
        flex: 0 1 23.4%; } }
    @media screen and (min-width: 1121px) {
      .technique__item {
        flex: 0 1 31.3%; } }
    @media screen and (min-width: 1285px) {
      .technique__item {
        flex: 0 1 22.9%; } }
    @media screen and (min-width: 1575px) {
      .technique__item {
        flex: 0 1 17.9%; } }
    .technique__item--arrow {
      margin-top: 104px;
      margin-right: 16px;
      margin-bottom: 16px; }
  .technique__title {
    display: inline-block;
    margin-top: 16px;
    margin-left: 16px; }
    @media screen and (max-width: 768px) {
      .technique__title {
        margin-bottom: 110px; } }

.content-height {
  height: auto; }
