@media screen and (max-width: 768px) {
  .swiper-slide {
    font-size: 18px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 16px;
    margin-right: 0;
    max-width: 240px; } }

@media screen and (max-width: 768px) {
  .swiper-slide img {
    display: block;
    object-fit: cover; } }

@media screen and (max-width: 768px) {
  .swiper-wrapper {
    margin-bottom: 66px;
    padding-left: 0; } }

@media screen and (min-width: 768px) {
  .swiper-wrapper {
    box-sizing: border-box;
    width: auto;
    transform: translate3d(0px, 0px, 0px) !important; } }

.swiper-slide {
  display: flex; }

.swiper-pagination-bullet-active {
  background: #524f4f; }

@media screen and (min-width: 768px) {
  .table__body.swiper-wrapper {
    display: table-row-group; } }

@media screen and (min-width: 768px) {
  .table.swiper {
    display: table; } }
