.form__input {
  display: block;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 16px;
  margin-left: 16px;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  border-radius: 8px;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  width: 90%; }

.form__text {
  display: inline-block;
  padding-left: 22px;
  padding-right: 32px;
  padding-bottom: 10px;
  color: #7E7E82;
  font-size: 14px;
  line-height: 16px; }

.form__link {
  text-decoration: none;
  color: #FF3E79; }

::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #B5B6BC; }

:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #B5B6BC; }

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #B5B6BC; }

:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #B5B6BC; }
