@media (min-width: 768px) {
  .description {
    display: flex;
    padding-bottom: 30px; } }

@media (min-width: 768px) {
  .description__container {
    width: 50%;
    padding-right: 40px;
    padding-left: 10px; } }

.description__button {
  border: none;
  outline: none;
  background-color: #F8F8F8;
  display: flex;
  margin-left: 16px;
  margin-bottom: 20px; }
  .description__button--title {
    font-size: 16px;
    margin-top: 5px;
    margin-left: 16px; }

.description__img {
  width: 100%;
  margin-top: 10px; }
  @media (min-width: 768px) {
    .description__img {
      width: 50%;
      border-radius: 10px;
      margin-right: 25px; } }

.description__text--first {
  margin-bottom: 0;
  margin-left: 20px;
  margin-right: 16px; }

.description__text--next {
  margin-top: 0;
  margin-left: 20px; }
  @media screen and (max-width: 768px) {
    .description__text--next {
      display: none; } }

@media (min-width: 768px) {
  .description__text--first {
    margin-right: 0; } }

@media (max-width: 1120px) {
  .description__text--full {
    display: none; } }

.description__text--open {
  display: none;
  margin-top: 0;
  margin-left: 20px;
  margin-right: 15px; }

.description__text--more {
  display: inline-block; }
