.user-menu {
  list-style-type: none;
  margin-left: auto;
  display: flex;
  position: absolute;
  top: 8px;
  right: 0; }
  @media (min-width: 510px) and (max-width: 1120px) {
    .user-menu {
      order: 999;
      padding-left: 30px; } }
  @media (min-width: 1120px) {
    .user-menu {
      order: 999;
      margin-left: 0; } }
  .user-menu__item {
    display: inline;
    margin-right: 4vw;
    display: inline-block; }
    @media (min-width: 510px) and (max-width: 1120px) {
      .user-menu__item {
        margin-right: 15px; } }
  .user-menu--style {
    display: inline-block;
    background: #FF3E79;
    padding: 10px 9px 10px 11px;
    text-decoration: none;
    border-radius: 100%;
    vertical-align: top;
    width: 43px;
    height: 43px; }
  .user-menu__link:hover {
    opacity: 0.5; }
  .user-menu__item {
    margin-right: 15px; }
  .user-menu__text {
    display: inline-block;
    margin-top: 10px;
    margin-right: 10px; }
    @media (max-width: 1120px) {
      .user-menu__text {
        display: none; } }
    @media (min-width: 1120px) {
      .user-menu__text {
        display: inline-block;
        margin-top: 10px;
        margin-right: 10px; } }

.user-menu {
  order: 999;
  padding-left: 30px; }

.user-menu {
  order: 999;
  margin-left: 0; }

@media (min-width: 1120px) {
  > *:not(.user-menu) {
    display: none; } }
