.read-more {
  display: block;
  position: relative;
  left: 57px;
  bottom: 34px;
  text-decoration: none;
  color: #1B1C21;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px; }
  @media screen and (max-width: 768px) {
    .read-more {
      display: none; } }
  .read-more::before {
    position: absolute;
    top: 6px;
    left: -20px;
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    border-left: 2px solid #41F6D7;
    border-bottom: 2px solid #41F6D7;
    transform: rotate(315deg); }
  .read-more::after {
    position: absolute;
    top: 11px;
    left: -20px;
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    border-left: 2px solid #41F6D7;
    border-bottom: 2px solid #41F6D7;
    transform: rotate(315deg); }
  .read-more--click::before {
    transform: rotate(137deg); }
  .read-more--click::after {
    transform: rotate(137deg); }

@media screen and (min-width: 1468px) {
  .read-more-l {
    display: none; } }
