.more--text {
  display: block;
  position: relative;
  left: 57px;
  bottom: 0;
  text-decoration: none;
  color: #1B1C21;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  width: 150px; }
  .more--text::before {
    position: absolute;
    top: 6px;
    left: -20px;
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    border-left: 2px solid #41F6D7;
    border-bottom: 2px solid #41F6D7;
    transform: rotate(315deg); }
  .more--text::after {
    position: absolute;
    top: 11px;
    left: -20px;
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    border-left: 2px solid #41F6D7;
    border-bottom: 2px solid #41F6D7;
    transform: rotate(315deg); }
  .more--text--click::before {
    transform: rotate(137deg); }
  .more--text--click::after {
    transform: rotate(137deg); }
