.navigation {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  overflow: auto;
  -ms-overflow-style: none; }
  .navigation__list::-webkit-scrollbar {
    width: 0;
    height: 0; }
  .navigation__list {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    list-style-type: none;
    overflow-x: hidden;
    padding: 20px;
    margin-left: 16px;
    white-space: nowrap;
    padding-left: 0;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    margin-top: 0; }
    @media (min-width: 1120px) {
