.price-policy {
  background-color: #F8F8F8; }
  .price-policy__text {
    display: block;
    min-width: 288px;
    max-width: 420px;
    color: #1B1C21;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-left: 32px;
    margin-right: 16px;
    padding-top: 32px;
    margin-top: 0; }
  .price-policy__link {
    display: block;
    font-size: 16px;
    line-height: 24px;
    color: #1B1C21;
    letter-spacing: -0.2px;
    position: relative;
    text-decoration: none;
    margin-left: 32px;
    font-weight: 900; }
    @media screen and (max-width: 350px) {
      .price-policy__link {
        width: 250px; } }
    .price-policy__link::after {
      position: absolute;
      top: 9px;
      left: 355px;
      content: '';
      display: block;
      width: 7px;
      height: 7px;
      border-left: 2px solid #FF3E79;
      border-bottom: 2px solid #FF3E79;
      transform: rotate(225deg); }
      @media screen and (max-width: 350px) {
        .price-policy__link::after {
          top: 33px;
          left: 110px; } }
