.container__divider::after {
  display: inline-flex;
  position: relative;
  bottom: 5px;
  right: 20px;
  width: 2px;
  height: 32px;
  left: 20px;
  text-align: center;
  content: "";
  border: 1px solid #EAEAEA; }
  @media screen and (min-width: 512px) {
    .container__divider::after {
      display: none; } }

.container__divider--m::after {
  display: inline-flex;
  position: relative;
  bottom: 25px;
  left: 35px;
  width: 2px;
  height: 30px;
  text-align: center;
  content: "";
  border: 1px solid #EAEAEA; }
  @media screen and (max-width: 512px) {
    .container__divider--m::after {
      display: none; } }

.container__divider--l::after {
  display: inline-block;
  position: relative;
  top: 8px;
  left: 30px;
  width: 2px;
  height: 30px;
  left: 12px;
  text-align: center;
  content: "";
  border: 1px solid #EAEAEA; }
  @media screen and (max-width: 512px) {
    .container__divider--l::after {
      display: none; } }
