.button--menu {
  outline: none;
  border: 0;
  margin-left: 2vw;
  margin-right: 2vw;
  display: block;
  background: #FF3E79;
  padding: 10px 9px 10px 11px;
  text-decoration: none;
  border-radius: 100%;
  vertical-align: top;
  width: 43px;
  height: 43px; }
  @media (min-width: 510px) and (max-width: 1120px) {
    .button--menu {
      margin-left: 10px;
      margin-right: 30px; } }
  .button--menu-img {
    padding-top: 3px; }
    @media screen and (min-width: 512px) {
      .button--menu-img {
        padding-right: 12px; } }
  .button--menu:hover {
    opacity: 0.5; }
  .button--menu-img {
    padding-right: 12px; }
