.nav {
  flex-grow: 1; }
  .nav__list {
    list-style-type: none;
    padding-left: 20px; }
    .nav__list:hover {
      padding-left: 20px; }
  .nav__item {
    margin-bottom: 20px;
    padding: 7px; }
    .nav__item:hover {
      padding-left: 3px;
      display: flex;
      align-items: center; }
  .nav__link {
    text-decoration: none;
    font-size: 16px;
    color: #1B1C21; }
    .nav__link:hover::before {
      position: relative;
      right: 24px;
      width: 2px;
      height: 30px;
      content: "";
      border: 2px solid #41F6D7; }
    .nav__link:hover {
      color: #7E7E82; }
