.brands {
  box-shadow: inset 0 2px 4px #D9FFF5;
  background-color: #F8F8F8; }
  @media screen and (max-width: 768px) {
    .brands::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      box-shadow: inset -40px 0 40px -4px white;
      z-index: 2;
      pointer-events: none; } }
  .brands__list {
    list-style-type: none;
    padding: 0; }
    @media screen and (min-width: 768px) {
      .brands__list {
        display: grid;
        margin-top: 24px;
        margin-right: 24px;
        margin-left: 24px;
        margin-bottom: 60px;
        height: 170px;
        overflow: hidden;
        transition: max-height 0.9s;
        grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
        -moz-column-gap: 32px;
             column-gap: 32px;
        row-gap: 16px; } }
  .brands__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 16px;
    padding-left: 16px;
    background-color: #FFFFFF;
    height: 72px;
    border: 1px solid #EAEAEA;
    border-radius: 6px; }
  .brands__arrow {
    display: block;
    position: relative;
    width: 40px;
    height: 40px;
    border: 2px solid #FF3E79;
    border-radius: 50%; }
    .brands__arrow::after {
      position: absolute;
      top: 13px;
      left: 13px;
      content: '';
      display: block;
      width: 9px;
      height: 9px;
      border-left: 2px solid #FF3E79;
      border-bottom: 2px solid #FF3E79;
      transform: rotate(225deg); }
    .brands__arrow:hover {
      opacity: 0.5; }
