html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Italic.eot) format("truetype");
  font-weight: normal;
  font-style: italic; }

body {
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: TT Lakes,'Arial', sans-serif;
  width: 100%; }
