.main-overflow-hidden {
  height: 100vh;
  max-height: 100vh;
  overflow-y: hidden; }

.main-overflow-auto {
  overflow: inherit; }

.modal--open {
  display: block;
  z-index: 5;
  position: fixed;
  background-color: #FFFFFF; }

.main--opacity {
  opacity: 0.2; }

.modal--close {
  display: none; }

.content-height {
  height: auto; }
