@charset "UTF-8";
.price {
  box-shadow: inset 0 2px 4px #D9FFF5;
  background-color: #F8F8F8; }
  @media screen and (max-width: 768px) {
    .price::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      box-shadow: inset -40px 0 40px -4px white;
      z-index: 2;
      pointer-events: none; } }
  .price__list {
    list-style-type: none;
    color: #1B1C21;
    letter-spacing: 0.2px;
    font-size: 14px;
    line-height: 18px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 16px; }
    @media screen and (max-width: 768px) {
      .price__list {
        padding-left: 0; } }
    @media screen and (min-width: 768px) {
      .price__list {
        padding-top: 63px;
        margin: 0;
        display: block; } }
  .price__link {
    background-color: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    padding: 23px 16px; }
    @media screen and (min-width: 768px) {
      .price__link {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
    @media screen and (max-width: 768px) {
      .price__link {
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.2px;
        position: relative;
        width: 206px;
        height: 142px;
        flex-flow: column;
        align-items: flex-start;
        padding-top: 36px;
        padding-bottom: 20px; } }
  @media screen and (min-width: 768px) {
    .price__service {
      width: 350px; } }
  @media screen and (max-width: 768px) {
    .price__service {
      position: relative;
      min-width: 240px;
      height: 36px; } }
  @media screen and (max-width: 768px) {
    .price__service::before {
      content: 'Ремонтные услуги';
      font-size: 14px;
      line-height: 16px;
      color: #7E7E82;
      opacity: 0.72;
      position: absolute;
      bottom: 40px; } }
  @media screen and (max-width: 768px) {
    .price__prices {
      position: relative; } }
  @media screen and (max-width: 768px) {
    .price__prices::before {
      content: 'Цена';
      font-size: 14px;
      line-height: 16px;
      color: #7E7E82;
      opacity: 0.72;
      position: absolute;
      bottom: 24px; } }
  @media screen and (max-width: 768px) {
    .price__time {
      position: relative; } }
  @media screen and (min-width: 768px) {
    .price__time {
      width: 81px; } }
  @media screen and (max-width: 768px) {
    .price__time::before {
      content: 'Срок';
      font-size: 14px;
      line-height: 16px;
      color: #7E7E82;
      opacity: 0.72;
      position: absolute;
      bottom: 24px; } }
  .price__btn {
    position: relative;
    background-color: #FF3E79;
    text-decoration: none;
    border-radius: 16px;
    vertical-align: top;
    color: #FFFFFF;
    padding: 7px 27px 7px 12px; }
    @media screen and (max-width: 768px) {
      .price__btn {
        position: absolute;
        bottom: 16px;
        right: 16px; } }
    .price__btn::after {
      position: absolute;
      top: 12px;
      left: 80px;
      content: '';
      display: block;
      width: 7px;
      height: 7px;
      border-left: 2px solid #FFFFFF;
      border-bottom: 2px solid #FFFFFF;
      transform: rotate(225deg); }
    .price__btn:hover {
      opacity: 0.5; }
