.order--btn {
  position: relative;
  background-color: #FF3E79;
  text-decoration: none;
  border-radius: 16px;
  vertical-align: top;
  color: #FFFFFF;
  padding: 4px 40px 4px 12px; }
  .order--btn::after {
    position: absolute;
    top: 12px;
    left: 105px;
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border-left: 2px solid #FFFFFF;
    border-bottom: 2px solid #FFFFFF;
    transform: rotate(225deg); }
  .order--btn:hover {
    opacity: 0.5; }
